import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import { convertDiscountMask, getFileName } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { tax_bases } = this.props.salesInvoice;
    const { loading } = this.props;

    const columnSalesInvoice = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "quantity" }),
        dataIndex: "qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "baseQuantity" }),
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "tax" }),
        dataIndex: "tax.tax_name"
      },
      {
        title: getLanguage({ id: "discount" }),
        dataIndex: "discount",
        align: "right",
        render: text => convertDiscountMask(text)
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const columnFreightCost = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no"
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "amount" }),
        dataIndex: "amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="purchaseInvoices.dueDate" />,
        description: data.payment_due_date
          ? moment(data.payment_due_date, "YYYY-MM-DD HH:mm:ss").format(
              "DD MMMM YYYY"
            )
          : null
      },
      {
        label: <IntlMessages id="purchaseInvoices.location" />,
        description: data.location_name
      },
      {
        label: <IntlMessages id="salesman" />,
        description: data.salesman_name ? data.salesman_name : "-"
      },
      {
        label: <IntlMessages id="purchaseOrders.shipTo" />,
        description: data.ship_to ? data.ship_to : "-"
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="purchaseInvoices.paymentCategory" />,
        description: data.payment_category
          ? data.payment_category.payment_category_code
          : null
      },
      {
        label: <IntlMessages id="purchaseInvoices.paymentTerm" />,
        description: data.payment_term
          ? data.payment_term.payment_term_code
          : null
      }
    ];

    let addition = [
      // {
      //   label: <IntlMessages id="salesInvoices.SODP" />,
      //   amount: data.sales_invoice_details
      //     ? data.sales_invoice_details[0]
      //       ? data.sales_invoice_details[0].sales_order
      //         ? data.sales_invoice_details[0].sales_order.total_down_payment
      //         : 0
      //       : 0
      //     : 0,
      //   parseAmount: true,
      //   labelAlign: "left",
      //   amountAlign: "left"
      // },
      {
        label: <IntlMessages id="purchaseInvoices.taxInvoiceNo" />,
        amount: data.tax_invoice_no,
        parseAmount: false,
        labelAlign: "left",
        amountAlign: "left"
      },
      {
        label: <span style={{ color: "transparent" }}>A</span>,
        amount: null,
        parseAmount: false,
        labelAlign: "left"
      },
      {
        label: (
          <strong>
            <IntlMessages id="taxBase" />
          </strong>
        ),
        amount: null,
        parseAmount: false,
        labelAlign: "left"
      }
    ];

    tax_bases.map(data =>
      addition.push({
        label: data.tax_name,
        amount: <NumberFormat value={data.tax_base} />,
        // parseAmount: true,
        labelAlign: "left",
        amountAlign: "left"
      })
    );

    const total = [
      {
        label: <IntlMessages id="purchaseInvoices.exlDiscountAndTax" />,
        amount: <NumberFormat value={data.total_exclude_discount_tax} />
        // parseAmount: true
      },
      {
        isDiscount: true,
        label: <IntlMessages id="purchaseInvoices.discount" />,
        discount: data.discount,
        amount: <NumberFormat value={data.discount_amount} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="purchaseInvoices.totalDiscount" />,
        amount: <NumberFormat value={data.total_discount} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="purchaseInvoices.totalTax" />,
        amount: <NumberFormat value={data.total_tax} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="purchaseInvoices.freightCost" />,
        amount: <NumberFormat value={data.total_freight_cost} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="total" />,
        amount: <NumberFormat value={data.total_amount} />
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.salesInvoice" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.sales_invoice_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="sidebar.customer" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>{data.customer_name}</strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={columnSalesInvoice}
                dataSource={data.sales_invoice_details}
                rowKey={row => row.id_sales_invoice_detail}
                pagination={false}
              />
            </Col>
            <Col span={12}>
              <Col span={15} style={{ marginTop: "15px", textAlign: "left" }}>
                <strong style={{ fontSize: "12px" }}>
                  <IntlMessages id="purchaseInvoices.totalQty" />
                </strong>
                <strong style={{ fontSize: "12px" }}>
                  <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "15px" }}>
                <strong style={{ fontSize: "12px" }}>
                  <NumberFormat value={data.total_qty} />
                </strong>
              </Col>
            </Col>
          </Row>

          {data && data.freight_costs && data.freight_costs.length > 0 ? (
            <Row style={{ marginTop: "24px" }}>
              <Divider
                orientation="left"
                style={{
                  fontSize: "12px",
                  alignItems: "center",
                  display: "flex"
                }}
              >
                <IntlMessages
                  id="purchaseInvoices.tabs.freightCost"
                  style={{ marginTop: "-10" }}
                />
              </Divider>
              <Col span={24}>
                <PrintTable
                  columns={columnFreightCost}
                  dataSource={data.freight_costs}
                  rowKey={row => row.id_purchase_invoice_cost}
                  pagination={false}
                />
              </Col>
            </Row>
          ) : null}

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "sales_invoice",
              data.sales_invoice_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
