import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SalesInvoiceUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import Button from "../../../../components/uielements/button";
import HistorySalesInvoice from "./history";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import Print from "./print";
import actions from "../../../../redux/sales/salesInvoice/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/sales/sales-invoices";

class UpdateSalesInvoice extends Component {
  state = {
    visibleModalHistory: false
  };
  componentDidMount() {
    const { editSalesInvoice } = this.props;
    const { id } = this.props.match.params;
    editSalesInvoice(id);
    // getTransactions(1, null, null);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesInvoice.error) {
      notification("error", this.props.salesInvoice.error);
      this.props.clearError();
    }
    if (this.props.salesInvoice.success) {
      notification("success", this.props.salesInvoice.success);
      this.props.clearSuccess();
    }
    if (this.props.salesInvoice.warning) {
      notification("warning", this.props.salesInvoice.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visibleModalHistory } = this.state;
    visibleModalHistory
      ? this.setState({ visibleModalHistory: false })
      : this.setState({ visibleModalHistory: true });
  };

  handleSubmit = value => {
    const { id } = this.props.match.params;
    const { updateSalesInvoice } = this.props;
    updateSalesInvoice(id, value);
  };

  handleSubmitCancelSI = value => {
    const { cancelSalesInvoice } = this.props;
    const { id } = this.props.match.params;

    cancelSalesInvoice(id, value);
  };

  render() {
    const { loading, salesInvoice } = this.props.salesInvoice;
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="salesInvoices.button.update" />}
          >
            <div
              style={{
                alignItems: "center",
                display: "flex"
              }}
            >
              <Tooltip placement="top" title="Info">
                <Button
                  style={{
                    border: 0,
                    fontSize: "24px",
                    paddingTop: "2px"
                    // color: theme.palette.warning
                  }}
                  icon="issues-close"
                  onClick={() => this.handleModalHistory()}
                  loading={loading}
                >
                  {/* <IntlMessages id="itemRequests.history" /> */}
                </Button>
              </Tooltip>

              <Print 
                loading={loading}
                data={salesInvoice}
                salesInvoice={this.props.salesInvoice}
              />
              <Button
                onClick={() => {
                  removeSaveData("si");
                  this.props.history.replace(redirecTo);
                }}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <HistorySalesInvoice
            visible={this.state.visibleModalHistory}
            handleModalHistory={this.handleModalHistory}
            description={salesInvoice ? salesInvoice.description : null}
          />

          {loading ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <SalesInvoiceUpdateForm
              handleSubmitCancelSI={this.handleSubmitCancelSI}
              submit={this.handleSubmit}
              salesInvoice={this.props.salesInvoice}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesInvoice: state.salesInvoice,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSalesInvoice);
