  renderAddress = id_address => {
    const { setFieldsValue } = this.props.form;
    const { customerAddresses } = this.state;
    const findAdd = customerAddresses.find(
      data => data.id_customer_address === id_address
    );
    var string =
      findAdd.customer_address_name +
      " - " +
      findAdd.address +
      (findAdd.district ? ", " + findAdd.district.district_name + ", " : "") +
      (findAdd.city
        ? findAdd.city.city_type + " " + findAdd.city.city_name + ", "
        : "") +
      (findAdd.province ? findAdd.province.province_name + ", " : "") +
      (findAdd.country ? findAdd.country.country_name : "");
    if (findAdd.zip_code) {
      string += " " + findAdd.zip_code;
    }
    setFieldsValue({ ship_to: string });
  };

  showTransaction = () => {
    const { prevTransConfirm } = this.props.purchaseInvoice;

    confirm({
      title: prevTransConfirm.title,
      content: prevTransConfirm.content,
      icon: <Icon type="info-circle" />,
      okText: prevTransConfirm.ok_button,
      cancelText: prevTransConfirm.cancel_button,
      onOk: () => {
        this.handleModalDeliveryOrder();
      },
      onCancel() {}
    });
  };

  syncSalesOrderDetail = (items, stateData) => {
    const { soDetails, custDisc, inclusive_tax, idSalesOrder, discBill } = this.state;
    let arrSO = [];
    items.map(data => arrSO.push(data.id_sales_order));
    const newSoDetails = soDetails.filter(data =>
      arrSO.includes(data.id_sales_order)
    );
    const setStateData = Object.assign({}, stateData, {
      soDetails: newSoDetails,
      idSalesOrder: newSoDetails.length === 0 
        ? null
        : idSalesOrder
    });

    if (discBill) {
      const fromPrevTrans = newSoDetails.length !== 0 ? true : false;
      this.setState(setStateData, () => this.calcDiscBill(setStateData.sum_subtotal_discount_item, fromPrevTrans))
    } else {
      setStateData.discount_amount = getSupplierDiscount(items, custDisc);
      setStateData.total_tax =
        items.length > 0
          ? getTotalTax(items, custDisc, inclusive_tax)
            ? getTotalTax(items, custDisc, inclusive_tax)
            : 0
          : 0;
      setStateData.total_exclude_discount_tax =
        items.length > 0 ? getExlDiscTax(items, inclusive_tax, custDisc) : 0;
      setStateData.total_discount = getTotalDiscount(items, custDisc, inclusive_tax);
      this.setState(setStateData);
    }
  };

  validateInvAmount = value => {
    const { total_amount } = this.state;
    if (parseFloat(total_amount) !== parseFloat(value)) {
      this.setState({ errInvAmount: true });
    } else {
      this.setState({ errInvAmount: false });
    }
  };

  updateSalesPrice = lastUpdatedTransDate => {
    const { update_sales_invoice_details, SITransactionDate, discountBills } = this.props.salesInvoice
    const { sum_subtotal_discount_item } = this.state;

    let setStateData = {}
    setStateData.calcDiscItem = true
    setStateData.items = update_sales_invoice_details
    setStateData.calculatingDiscount = true
    setStateData.discBill = discountBills[0] ? discountBills[0] : {}

    this.setState(setStateData, () => this.calcDiscBill(sum_subtotal_discount_item, false, true))
    this.props.form.setFieldsValue({
      transaction_date: moment(SITransactionDate, "YYYY-MM-DD HH:mm:ss")
    })
    this.handleModalPriceCompare()
  }

